import axios from 'axios';  // axiosHTTP
import { DEEPSEEK_API_KEY } from '../config';  // configDEEPSEEK_API_KEY

const deepseekReuqest = axios.create({  // axios
  baseURL: 'https://api.deepseek.com',  // URL
  timeout: 10000,  // 10
  withCredentials: true,  // 
  headers: {  // 
    Authorization: `Bearer ${DEEPSEEK_API_KEY}`  // AuthorizationBearer Token
  }
});

deepseekReuqest.interceptors.request.use((config) => {  // 
  return config;  // 
})

deepseekReuqest.interceptors.response.use((response) => {  // 
  return response;  // 
})

export { deepseekReuqest };  // deepseekReuqest
